.Home{
    width: 100%;
    background-color: #161616;
}
.Home-body{
    width: 100%;
}
.Home-backgroundImageContainer{
    /* position: relative; */
    display: flex;
    /* align-items: center;
    justify-content: center; */
}
.Home-backgroundImage{
    width: 100%;
    margin-bottom: -5px;
    margin-top: -5px;
    min-height: 97vw;
}

.home-bodyModal-Desktop{
    position: absolute;
    display: flex;
    align-items: center;
    
    flex-direction: column;
    background-color:  rgba(0, 0, 0, 0.9);
    padding:24px 40px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 160px;
    top:42vh;
    left:0;
    right:0;
    width: 400px;
    opacity: 0.98;
    margin-top: auto;
    margin-right: auto;
   
}
.home-bodyModal-Mobile{
    display: none;
    align-items: center;
    flex-direction: column;
    background-color:  rgba(0, 0, 0, 0.9);
    padding:24px 16px;
    width: 78%;
    opacity: 0.97;
    margin-top: 38px;
    margin-bottom: 50px;
}
.home-bodyModalTitle{
    color:#E2E2E2;
    font-size: 30px;
    font:38px 'Montserrat',sans-serif; 
    text-align:center;
    white-space: pre-line;
    display: inline-block;
    margin-bottom: 15px;
}
.home-bodyModalText{
   
    color:#E2E2E2;
    font-size: 22px;
    font-family:'Roboto', Arial, sans-serif;
    letter-spacing: 1px;
    margin-bottom: 15px;
}
.home-bodyModalButton{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    text-align: center;
    color:#303030;
    padding:20px 32px;
    max-width: 200px;
    background-color: #FFFFFF;
    font-size: 16px;
    font-family:'Roboto', Arial, sans-serif;
    letter-spacing: 3px;
    min-height: 86px;
    font-weight: 700;
    border-radius: 3px;
    cursor:pointer;
    margin-top: 20px;
    text-decoration: none;
    margin-bottom: 20px;
}
.Home-socialDiv{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    height: 295px;
    background-color: #161616;
}
.Home-socialDivText{
    color:#636363;
    font-size: 30px;
    font:44px 'Montserrat',sans-serif; 
    text-align:center;
    white-space: pre-line;
    display: inline-block;
}
.Home-socialDivDivider{
    width:50px;
    margin:30px 0px 42px;
    border-bottom: 0.125em solid rgb(48, 48, 48);
   
}
.Home-socialDivIconHolder{
    display: flex;
}
.Home-socialDivIcon,.Home-socialDivIcon2{
    display: flex;
    justify-content: center;
    align-items: center;
    
    background-color: #E2E2E2;
    border-radius: 360px;
    height: 35px;
    width: 35px;
}
.Home-socialDivIcon{
    /* margin-right: 25px; */
}
.Home-socialDivIcon2{
    /* margin-left: 25px; */
}
.timer{
    /* background-color: red; */
    height: 85px;
    width:260px;
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
    font-size: 25px;
    font-family:'Roboto', Arial, sans-serif;
}

.timer-child{
   height: 100%;
   width: 19%;
   display: flex;
   flex-direction: column;
   /* align-items: center; */
}
.timer-child>span{
    display:flex;
    align-items: center;
    justify-content: center;
    height: 32%;
    font-size: 12px;
    color: #E2E2E2;
    font-family:'Roboto', Arial, sans-serif;
    /* background-color: blue; */
}
.timer-childInner{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height:68%;
    /* background-color: blue; */
    border-radius: 3px;
    background-color: #E2E2E2;
    
}

@media screen and (min-width: 1024px) {
    .home-bodyModal-Desktop{
        width: 320px;
    }
    .home-bodyModalTitle{
        font:44px 'Montserrat',sans-serif; 
    }
}
@media screen and (min-width: 1550px) {
    .home-bodyModal-Desktop{
        width: 380px;
        top:59vh;
    }
    .home-bodyModalTitle{
        font:48px 'Montserrat',sans-serif; 
    }
    .home-bodyModalText{
        font-size: 24px;
    }
    .Home-socialDiv{
        height: 301px;
    }
}
@media screen and (max-width: 1023px) {
    .home-bodyModal-Desktop{
        display:none;
    }
    .home-bodyModal-Mobile{
        display: flex;
    }

    .Home-socialDivText{
        font-size: 32px;
    }
    .Home-socialDiv{
        height:fit-content;
        padding-bottom: 50px;
    }
  
    svg{
        height: 40px;
        width:40px;
    }
    .home-bodyModalTitle{
        font-size: 32px;
    }
   
}
@media screen and (max-width: 360px) {

    .home-bodyModalTitle{
        font-size: 28px;
    }
   
}