.Footer{
    display: flex;
    align-items: center;
    justify-content: center;
    height:134px;
    width:100%;
    background-color: #161616;
    border-top:1px solid rgb(42, 42, 42);
    
}
.Footer-text{
    color:#A9A9A9;
    font-size:14px;
    font-family:'Roboto', Arial, sans-serif;
    text-align: center;
    max-width: 85%;
    line-height: 1.5;
}
@media screen and (min-width: 1550px) {
    .Footer-text{
        font-size:16px;
    }
    .Footer{
        height:137px;
    }
}
@media screen and (max-width: 500px) {
    .Footer{
        height:123px;
    }
}