.Carousel{
    width: 100%;
    background-color: #000000;
    padding:56px 0px;
}
.Carousel-Header{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.Carousel-Header-title{
    color:#636363;
    font-size: 30px;
    font:44px 'Montserrat',sans-serif; 
    text-align:center;
    white-space: pre-line;
    display: inline-block;
}
.Carousel-Divider{
    width:50px;
    margin:30px 0px 42px;
    border-bottom: 0.125em solid rgb(31, 31, 31);
}
.Carousel-Footer-Image{
    height: 70px;
    width: 70px;
    margin-left:10px;
    cursor: pointer;
    border:1px solid rgb(31, 31, 31)
}
.Carousel-BodyImage{
    height:600px;
    width:600px;
    margin-left: 5px;
    transition: opacity 420ms ease-in-out 0s,
}
.Carousel-Footer{
    width: 1125px;
    margin-left: auto;
    margin-right: auto;
   
}
.Carousel-Body{
    width: 1115px;
    height: 600px;
    display: flex;
    overflow-x: hidden;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
    position: relative;
    touch-action: pan-y pinch-zoom;
    position: relative;
    align-items: center;
}
.Carousel-InnerBodyChild{
    display: flex;
}
.Carousel-InnerBody2{
    width: 7260px;
    display: flex;
    transform: translateX(-967px);
    transition: none 0s ease 0s;
    transition: transform 500ms ease-in-out 0s;
}
.Carousel-InnerBody{
    position:absolute;
    display: flex;
    touch-action: pan-y pinch-zoom;
}
.Carousel-ButtonPrevious, .Carousel-ButtonNext{
    background-color:rgba(22, 22, 22, 0.5);
    padding:8px 4px 8px 2px;
    position: absolute;
    color:white;
    display: flex;
    align-items: center;
    border:none;
    cursor:pointer;
}
.Carousel-ButtonNext{
    padding:8px 2px 8px 4px;
    right:0;
}


@media screen and (max-width: 1150px) and (min-width: 775px){
    .Carousel-BodyImage{
        height:400px;
        width:400px;
    }
    .Carousel-Body{
        width: 741px;
        height: 400px;
    }
    .Carousel-Footer{
      
        width:741px;
    }
}
@media screen and (max-width: 774px)  {
    .Carousel-BodyImage{
        height:210px;
        width:210px;
    }
    .Carousel-Body{
        width: 389px;
        height: 210px;
    }
    .Carousel-Footer{
        display:none;
    }
    .Carousel-Header-title{
        font:32px 'Montserrat',sans-serif;   
    }
    .Carousel{
        margin-bottom: -35px;
    }
}
@media screen and (max-width: 449px) {
    
    .Carousel-Body{
        width: 330px;
        height: 210px;
    }
   
}
@media screen and (max-width: 355px) {
    
    .Carousel-Body{
        width: 290px;
        height: 210px;
    }
    
}

