

.Header{
    width: 100%;
    height: 84px;
    background-color: #161616;
    font-family: 'Roboto', arial, sans-serif;
}
.Header-top{
    display: flex;
    align-items: center;
    justify-content: center;
    height:100px;
    background-color: #c75bcb;
    height: 84px;
}
.Header-bottom{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(0, 0, 0);
    height: 56px;
}
.Header-title{
    color:white;
    font-size: 30px;
    font-weight: 549;
    font:30px 'Montserrat',sans-serif; 
    cursor: pointer;
}
.Header-navTitle{
    color:#E2E2E2;
    font-size: 14px;
    font-family:'Roboto', Arial, sans-serif;
    letter-spacing: 2px;
    margin: 10px;
    padding:6px;
    cursor: pointer;
}
.Header-navTitle:hover{
    color:#b4b4b4;
}
.Header-hometitle{
    color:#838383;
}
.Header-menuIcon{
    color:rgb(226, 226, 226);
    margin-left:30px;
    display: none;
}
@media screen and (min-width: 1300px) {
    .Header-title{
        font-size: 32px;
    }
  
}
@media screen and (min-width: 1550px) {
    .Header-title{
        font-size: 36px;
    }
    .Header{
        height: 91px;
    }
    .Header-navTitle{
        font-size: 16px;
    }
    .Header-top{
        height: 91px;
    }
    
}
@media screen and (max-width: 1023px) {
    .Header{
        height: 81px;
    }
    .Header-title{
        font-size: 28px;
    }
    .Header-navTitle{
        display: none;
    }
    .Header-top{
        height: 81px;
    }
    .Header-bottom{
        height: 72px;
        justify-content: start;
        
    }
    .Header-menuIcon{
        display: flex;
    }
}