@import url(https://fonts.googleapis.com/css?family=Montserrat:600);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin:0;
    padding:0;
}



.Header{
    width: 100%;
    height: 84px;
    background-color: #161616;
    font-family: 'Roboto', arial, sans-serif;
}
.Header-top{
    display: flex;
    align-items: center;
    justify-content: center;
    height:100px;
    background-color: #c75bcb;
    height: 84px;
}
.Header-bottom{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(0, 0, 0);
    height: 56px;
}
.Header-title{
    color:white;
    font-size: 30px;
    font-weight: 549;
    font:30px 'Montserrat',sans-serif; 
    cursor: pointer;
}
.Header-navTitle{
    color:#E2E2E2;
    font-size: 14px;
    font-family:'Roboto', Arial, sans-serif;
    letter-spacing: 2px;
    margin: 10px;
    padding:6px;
    cursor: pointer;
}
.Header-navTitle:hover{
    color:#b4b4b4;
}
.Header-hometitle{
    color:#838383;
}
.Header-menuIcon{
    color:rgb(226, 226, 226);
    margin-left:30px;
    display: none;
}
@media screen and (min-width: 1300px) {
    .Header-title{
        font-size: 32px;
    }
  
}
@media screen and (min-width: 1550px) {
    .Header-title{
        font-size: 36px;
    }
    .Header{
        height: 91px;
    }
    .Header-navTitle{
        font-size: 16px;
    }
    .Header-top{
        height: 91px;
    }
    
}
@media screen and (max-width: 1023px) {
    .Header{
        height: 81px;
    }
    .Header-title{
        font-size: 28px;
    }
    .Header-navTitle{
        display: none;
    }
    .Header-top{
        height: 81px;
    }
    .Header-bottom{
        height: 72px;
        justify-content: start;
        
    }
    .Header-menuIcon{
        display: flex;
    }
}
.Footer{
    display: flex;
    align-items: center;
    justify-content: center;
    height:134px;
    width:100%;
    background-color: #161616;
    border-top:1px solid rgb(42, 42, 42);
    
}
.Footer-text{
    color:#A9A9A9;
    font-size:14px;
    font-family:'Roboto', Arial, sans-serif;
    text-align: center;
    max-width: 85%;
    line-height: 1.5;
}
@media screen and (min-width: 1550px) {
    .Footer-text{
        font-size:16px;
    }
    .Footer{
        height:137px;
    }
}
@media screen and (max-width: 500px) {
    .Footer{
        height:123px;
    }
}
.Home{
    width: 100%;
    background-color: #161616;
}
.Home-body{
    width: 100%;
}
.Home-backgroundImageContainer{
    /* position: relative; */
    display: flex;
    /* align-items: center;
    justify-content: center; */
}
.Home-backgroundImage{
    width: 100%;
    margin-bottom: -5px;
    margin-top: -5px;
    min-height: 97vw;
}

.home-bodyModal-Desktop{
    position: absolute;
    display: flex;
    align-items: center;
    
    flex-direction: column;
    background-color:  rgba(0, 0, 0, 0.9);
    padding:24px 40px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 160px;
    top:42vh;
    left:0;
    right:0;
    width: 400px;
    opacity: 0.98;
    margin-top: auto;
    margin-right: auto;
   
}
.home-bodyModal-Mobile{
    display: none;
    align-items: center;
    flex-direction: column;
    background-color:  rgba(0, 0, 0, 0.9);
    padding:24px 16px;
    width: 78%;
    opacity: 0.97;
    margin-top: 38px;
    margin-bottom: 50px;
}
.home-bodyModalTitle{
    color:#E2E2E2;
    font-size: 30px;
    font:38px 'Montserrat',sans-serif; 
    text-align:center;
    white-space: pre-line;
    display: inline-block;
    margin-bottom: 15px;
}
.home-bodyModalText{
   
    color:#E2E2E2;
    font-size: 22px;
    font-family:'Roboto', Arial, sans-serif;
    letter-spacing: 1px;
    margin-bottom: 15px;
}
.home-bodyModalButton{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    text-align: center;
    color:#303030;
    padding:20px 32px;
    max-width: 200px;
    background-color: #FFFFFF;
    font-size: 16px;
    font-family:'Roboto', Arial, sans-serif;
    letter-spacing: 3px;
    min-height: 86px;
    font-weight: 700;
    border-radius: 3px;
    cursor:pointer;
    margin-top: 20px;
    text-decoration: none;
    margin-bottom: 20px;
}
.Home-socialDiv{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    height: 295px;
    background-color: #161616;
}
.Home-socialDivText{
    color:#636363;
    font-size: 30px;
    font:44px 'Montserrat',sans-serif; 
    text-align:center;
    white-space: pre-line;
    display: inline-block;
}
.Home-socialDivDivider{
    width:50px;
    margin:30px 0px 42px;
    border-bottom: 0.125em solid rgb(48, 48, 48);
   
}
.Home-socialDivIconHolder{
    display: flex;
}
.Home-socialDivIcon,.Home-socialDivIcon2{
    display: flex;
    justify-content: center;
    align-items: center;
    
    background-color: #E2E2E2;
    border-radius: 360px;
    height: 35px;
    width: 35px;
}
.Home-socialDivIcon{
    /* margin-right: 25px; */
}
.Home-socialDivIcon2{
    /* margin-left: 25px; */
}
.timer{
    /* background-color: red; */
    height: 85px;
    width:260px;
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
    font-size: 25px;
    font-family:'Roboto', Arial, sans-serif;
}

.timer-child{
   height: 100%;
   width: 19%;
   display: flex;
   flex-direction: column;
   /* align-items: center; */
}
.timer-child>span{
    display:flex;
    align-items: center;
    justify-content: center;
    height: 32%;
    font-size: 12px;
    color: #E2E2E2;
    font-family:'Roboto', Arial, sans-serif;
    /* background-color: blue; */
}
.timer-childInner{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height:68%;
    /* background-color: blue; */
    border-radius: 3px;
    background-color: #E2E2E2;
    
}

@media screen and (min-width: 1024px) {
    .home-bodyModal-Desktop{
        width: 320px;
    }
    .home-bodyModalTitle{
        font:44px 'Montserrat',sans-serif; 
    }
}
@media screen and (min-width: 1550px) {
    .home-bodyModal-Desktop{
        width: 380px;
        top:59vh;
    }
    .home-bodyModalTitle{
        font:48px 'Montserrat',sans-serif; 
    }
    .home-bodyModalText{
        font-size: 24px;
    }
    .Home-socialDiv{
        height: 301px;
    }
}
@media screen and (max-width: 1023px) {
    .home-bodyModal-Desktop{
        display:none;
    }
    .home-bodyModal-Mobile{
        display: flex;
    }

    .Home-socialDivText{
        font-size: 32px;
    }
    .Home-socialDiv{
        height:-webkit-fit-content;
        height:-moz-fit-content;
        height:fit-content;
        padding-bottom: 50px;
    }
  
    svg{
        height: 40px;
        width:40px;
    }
    .home-bodyModalTitle{
        font-size: 32px;
    }
   
}
@media screen and (max-width: 360px) {

    .home-bodyModalTitle{
        font-size: 28px;
    }
   
}
.Carousel{
    width: 100%;
    background-color: #000000;
    padding:56px 0px;
}
.Carousel-Header{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.Carousel-Header-title{
    color:#636363;
    font-size: 30px;
    font:44px 'Montserrat',sans-serif; 
    text-align:center;
    white-space: pre-line;
    display: inline-block;
}
.Carousel-Divider{
    width:50px;
    margin:30px 0px 42px;
    border-bottom: 0.125em solid rgb(31, 31, 31);
}
.Carousel-Footer-Image{
    height: 70px;
    width: 70px;
    margin-left:10px;
    cursor: pointer;
    border:1px solid rgb(31, 31, 31)
}
.Carousel-BodyImage{
    height:600px;
    width:600px;
    margin-left: 5px;
    transition: opacity 420ms ease-in-out 0s
}
.Carousel-Footer{
    width: 1125px;
    margin-left: auto;
    margin-right: auto;
   
}
.Carousel-Body{
    width: 1115px;
    height: 600px;
    display: flex;
    overflow-x: hidden;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
    position: relative;
    touch-action: pan-y pinch-zoom;
    position: relative;
    align-items: center;
}
.Carousel-InnerBodyChild{
    display: flex;
}
.Carousel-InnerBody2{
    width: 7260px;
    display: flex;
    -webkit-transform: translateX(-967px);
            transform: translateX(-967px);
    transition: none 0s ease 0s;
    transition: -webkit-transform 500ms ease-in-out 0s;
    transition: transform 500ms ease-in-out 0s;
    transition: transform 500ms ease-in-out 0s, -webkit-transform 500ms ease-in-out 0s;
}
.Carousel-InnerBody{
    position:absolute;
    display: flex;
    touch-action: pan-y pinch-zoom;
}
.Carousel-ButtonPrevious, .Carousel-ButtonNext{
    background-color:rgba(22, 22, 22, 0.5);
    padding:8px 4px 8px 2px;
    position: absolute;
    color:white;
    display: flex;
    align-items: center;
    border:none;
    cursor:pointer;
}
.Carousel-ButtonNext{
    padding:8px 2px 8px 4px;
    right:0;
}


@media screen and (max-width: 1150px) and (min-width: 775px){
    .Carousel-BodyImage{
        height:400px;
        width:400px;
    }
    .Carousel-Body{
        width: 741px;
        height: 400px;
    }
    .Carousel-Footer{
      
        width:741px;
    }
}
@media screen and (max-width: 774px)  {
    .Carousel-BodyImage{
        height:210px;
        width:210px;
    }
    .Carousel-Body{
        width: 389px;
        height: 210px;
    }
    .Carousel-Footer{
        display:none;
    }
    .Carousel-Header-title{
        font:32px 'Montserrat',sans-serif;   
    }
    .Carousel{
        margin-bottom: -35px;
    }
}
@media screen and (max-width: 449px) {
    
    .Carousel-Body{
        width: 330px;
        height: 210px;
    }
   
}
@media screen and (max-width: 355px) {
    
    .Carousel-Body{
        width: 290px;
        height: 210px;
    }
    
}


